@media screen and (max-width: 1500px) {
  .pages-main-wrap {
    padding: 50px 100px !important;
  }
  .map-wrap {
    padding: 100px 50px !important;
  }
  footer {
    padding: 60px 80px !important;
  }
}

@media screen and (max-width: 1350px) {
  .games-wrap .games-grid .games-box .content-and-image .image-container .swiper .swiper-slide img {
    border-top-left-radius: 18px !important;
    border-top-right-radius: 18px !important;
  }
  .image-container {
    border-bottom-left-radius: 0 !important;
  }
  .first-section {
    border-radius: 0 !important;
    .swiper {
      border-radius: 0 !important;
    }
  }
  .map-left-wrap {
    .left-content {
      padding: 40px !important;
    }
  }
}

@media screen and (max-width: 1300px) {
  .news-wrap,
  .journal-wrap {
    padding: 0 20px !important;
  }
  .games-wrap {
    padding: 0 20px !important;
  }
  .game-boxes,
  .news-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  .game-box {
    margin-right: 0 !important;
  }
  .second-section,
  .third-section {
    padding: 50px;
  }
  //게임쪽
  .content-and-image {
    display: inline-block !important;
  }
  .games-content {
    display: inline-block !important;
  }
  .games-wrap {
    .games-grid {
      .games-box {
        .content-and-image > div {
          width: 100% !important;
        }
      }
    }
  }
}

@media screen and (max-width: 1050px) {
  .first-section {
    max-width: 1400px;
    margin: 0 auto;
    height: 70vw;
    background-color: transparent;
    border-radius: 18px;
    overflow: hidden;
    position: relative;

    &::after {
      position: absolute;
      width: 100%;
      height: 100%;
      content: '';
      top: 0;
      left: 0;
      z-index: 4;
      pointer-events: none; /* 클릭 불가능 */
    }

    .swiper {
      width: 100%;
      height: 100%;
      border-radius: 0 !important;
      position: relative;

      .swiper-slide {
        width: 100%;
        height: 100%;
        display: flex; /* Flexbox 추가 */
        align-items: flex-start; /* 최상단 고정 */
      }

      .slide-image {
        background-position: top center; /* 상단 중앙에 고정 */
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
        background-size: contain; /* 이미지 크기를 contain으로 설정 */
        position: relative; /* 부모 요소에 대해 상대적으로 위치 설정 */
      }
    }
  }
  .intro-img-wrap {
    padding-top: 0 !important;
  }
  .map-wrap {
    display: inline-block !important;
    width: 100% !important;
    .map-left-wrap,
    .map-right-wrap {
      display: inline-block !important;
      width: 100% !important;
    }
    .map-left-wrap {
      border-top-right-radius: 18px !important;
      border-bottom-left-radius: 0 !important;
    }
    .map-right-wrap {
      border-bottom-left-radius: 18px !important;
      border-top-right-radius: 0 !important;
    }
    .map-btn-wrap {
      display: flex;
      justify-content: center;
    }
    .come-a p,
    .come-b p {
      padding: 6px 18px !important;
    }
    .come-a,
    .come-b {
      margin-top: 15px;
    }
    .come-b {
      padding-left: 15px;
    }
  }
}

@media screen and (max-width: 1000px) {
  .board-detail-wrap {
    padding: 20px 20px !important;
  }
  .mobile-main-title {
    padding: 11px 0 0 20px;
    font-size: 1.6em;
  }
  .header-box {
    padding: 0 20px !important;
  }

  // .main-logo {
  //   display: none !important;
  // }

  .header-right-box {
    padding-top: 0 !important;
    z-index: 1500;
  }

  .header-right-box ul li {
    display: none !important;
  }

  .hamburger {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease;

    &.active {
      transform: rotate(360deg);
    }

    .bar {
      width: 30px;
      height: 4px;
      background: black;
      margin: 2px 0;
      transition: background 0.3s, transform 0.3s, height 0.3s;

      &.rotate {
        &:first-child {
          transform: rotate(45deg) translate(0, 8px); /* 위치 조정 */
          height: 3px;
        }
        &:nth-child(2) {
          opacity: 0;
          height: 0;
        }
        &:last-child {
          transform: rotate(-45deg) translate(0, -8px); /* 위치 조정 */
          height: 3px;
        }
      }
    }
  }

  .mobile-header {
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    background: white;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 1499;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 20px 0 0 0;

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%; /* 하단 너비를 맞춤 */
      height: 30px; /* 접힌 부분의 높이 */
      background: white; /* 배경색을 흰색으로 설정 */
      transform: skewY(-35deg); /* 오른쪽 위로 접힌 느낌을 주기 위해 기울임 */
      transform-origin: bottom left; /* 기울임의 기준점 설정 */
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* 그림자 추가 */
    }

    .mobile-sub-total {
      list-style: none;
      padding: 40px 0 0 4px;

      li {
        padding: 10px 20px;
        cursor: pointer;
        transition: background 0.3s;
      }
    }
    .mobile-submenu {
      padding-top: 15px;
    }
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: calc(100% - 300px);
    height: 100vh;
    background: rgba(30, 34, 40, 0.7);
    z-index: 1498;
  }

  .arrow-up {
    background-image: url('../assets/images/arrow_up.png');
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-left: 10px;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .arrow-down {
    background-image: url('../assets/images/arrow_down.png');
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-left: 10px;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .board-main-wrap {
    width: 100% !important;
    flex: 0 !important;
    padding: 0 !important;

    .content-left-box,
    .content-right-box {
      display: block !important;
      float: none !important;
      width: 100% !important;
      padding: 0 !important;
      text-align: center;
    }
  }
}

